import React, { useState } from "react";
import logo from "./../assets/logo.svg";
import { Link } from "react-router-dom";

const Navbar = () => {
  const [click, setClick] = useState(false);
  const handleClick = () => {
    setClick(!click);
  };
  const closeMobileMenu = () => setClick(false);
  return (
    <>
      <nav className="container-fluid">
        <div className="d-flex justify-content-around py-3 align-items-center">
          <div className="logo" onClick={closeMobileMenu}>
            <img src={logo} alt="" />
          </div>
          <ul
            className={
              click
                ? "d-flex align-items-center nav-menu active"
                : "d-flex align-items-center nav-menu"
            }
          >
            <li className="mx-3">
              <Link
                to="/"
                onClick={closeMobileMenu}
                className="nav-links active"
              >
                Home
              </Link>
            </li>
            <li className="mx-3">
              <Link to="/" onClick={closeMobileMenu} className="nav-links">
                About
              </Link>
            </li>
            <li className="mx-3">
              <Link to="/" onClick={closeMobileMenu} className="nav-links">
                Places
              </Link>
            </li>
            <li className="mx-3">
              <Link to="/" onClick={closeMobileMenu} className="nav-links">
                Career
              </Link>
            </li>
            <li className="mx-3">
              <Link to="/" onClick={closeMobileMenu} className="nav-links">
                Blog
              </Link>
            </li>
          </ul>
          <div className="connect">
            <button className="btn btn-primary button">Connect</button>
          </div>
          <div className="menu-icon" onClick={handleClick}>
            <i className={click ? "fa fa-times fa-2x" : `fa fa-bars fa-2x`}></i>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
